// this part is for user comments
import React, { Component } from "react";
import firebase from "./firebase";
import StarRating from "./Rating";
class Comments extends Component {
  constructor(props) {
    super(props);
    this.state = {
      comments: [],
      userInput: "",
    };
  }

  componentDidMount() {
    const dbRef = firebase.database().ref();
    dbRef.on("value", (snapshot) => {
      console.log(snapshot.val());

      const data = snapshot.val();
      const userComments = [];

      for (let userName in data) {
        const allItems = {
          id: userName,
          comment: data[userName],
        };
        userComments.push(allItems);
      }
      console.log(userComments);
      this.setState({ comments: userComments });
      console.log("worked");
    });
  }

  handleChange = (e) => {
    this.setState({
      userInput: e.target.value,
    });
  };

  handleClick = (e) => {
    e.preventDefault();
    const dbRef = firebase.database().ref();
    dbRef.push(this.state.userInput);
    this.setState({ userInput: "" });
    
  };

  deleteComment = (id) => {
    const dbRef = firebase.database().ref();
    dbRef.child(id).remove();
  };

  render() {
    return (
      // get user's comments
      <section className="userInput">
        <form action="submit">
          <label htmlFor="comments" className="sr-only">
            comments
          </label>
          <textarea
            id="comments"
            rows="5"
            cols="40"
            value={this.state.userInput}
            placeholder="write a review"
            onChange={this.handleChange}
            required
          />                               
          <div>
            <button onClick={this.handleClick}>post review</button>
          </div>
        </form>
        <ul className="listItem">
          {/* map through each comment inside of comments array */}
          {this.state.comments.map((comment) => {
            return (
              <li key={comment.id}>
                <p>{comment.comment}</p>
                <div>
                  <button
                    onClick={() => {
                      this.deleteComment(comment.id);
                    }}
                  >
                    Delete
                  </button>
                </div>
              </li>
            );
          })}
        </ul>
      </section>
    );
  }
}

export default Comments;
